.pricing-container {
  width: 100%;
  display: flex;
  min-height: 100vh;
  align-items: center;
  flex-direction: column;
}
.pricing-container1 {
  width: 100%;
  height: 122px;
  display: flex;
  align-items: flex-start;
  justify-content: flex-end;
}
.pricing-container2 {
  flex: 0 0 auto;
  width: 100%;
  height: 100%;
  display: flex;
  position: relative;
  align-items: center;
  justify-content: flex-end;
}
.pricing-navlink {
  font-size: 20px;
  font-style: normal;
  font-weight: 700;
  margin-left: 20px;
  margin-right: 30px;
  text-decoration: none;
}
.pricing-image {
  top: 4px;
  left: 0px;
  right: 0px;
  width: 100px;
  height: 117px;
  margin: auto;
  position: absolute;
  object-fit: cover;
}
.pricing-pricing {
  width: 100%;
  display: flex;
  justify-content: center;
  background-color: #f5f5f5ff;
}
.pricing-container3 {
  width: 100%;
  display: flex;
  padding: var(--dl-space-space-threeunits);
  max-width: var(--dl-size-size-maxwidth);
  align-items: flex-start;
  justify-content: center;
}
.pricing-container4 {
  flex: 1;
  display: flex;
  align-items: flex-start;
  margin-right: var(--dl-space-space-twounits);
  flex-direction: column;
}
.pricing-text {
  width: 224px;
}
.pricing-text01 {
  color: var(--dl-color-gray-500);
  width: 60%;
  margin-top: var(--dl-space-space-twounits);
  text-align: left;
  margin-bottom: var(--dl-space-space-twounits);
}
.pricing-container5 {
  flex: 2;
  width: 50%;
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: flex-end;
}
.pricing-container6 {
  flex: 1;
  height: 450px;
  display: flex;
  max-width: 450px;
  box-shadow: 5px 5px 10px 0px #d4d4d4;
  transition: 0.3s;
  align-items: center;
  padding-top: var(--dl-space-space-twounits);
  margin-right: var(--dl-space-space-twounits);
  padding-left: var(--dl-space-space-twounits);
  padding-right: var(--dl-space-space-twounits);
  flex-direction: column;
  padding-bottom: var(--dl-space-space-twounits);
  background-color: var(--dl-color-gray-white);
}
.pricing-container6:hover {
  transform: scale(1.02);
}
.pricing-text05 {
  font-size: 1.5rem;
